//Contenu général
//scss/content/_main.scss

//Appel de fonts
@include font-face('poppins', '../fonts/poppins-light', 300, normal);
@include font-face('poppins', '../fonts/poppins-regular', 400, normal);
@include font-face('poppins', '../fonts/poppins-bold', 700, normal);


html, body{
  width: 100%;
  height: 100%;
  font-family: "poppins", Arial, Helvetica, sans-serif;
  font-weight: 400;
}

body{
  color: $body-color;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6{
  color: $title-color;
  font-weight: 700;
}

.section{
  padding: 80px 0;
}

//Titres section
.section-title{
  position: relative;
  &:before{
    content: "";
    position: absolute;
    height: 2px;
    width: 2rem;
    background: #000;
  }
}

.section-title{
  &.text-left{
    padding-left: 2.5rem;
    &:before{
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
    }
  }

  &.text-center{
    &:before{
      left: 50%;
      top: -1rem;
      transform: translate(-50%, 0);
    }
  }

  &.text-right{
    padding-right: 2.5rem;
    &:before{
      right: 0;
      top: 50%;
      transform: translate(0, -50%);
    }
  }
}


button, a{
  &:focus{
    outline: $focus-base;
  }
}

#main-content{
  margin-top: 0;
  transition: margin-top .4s ease-in-out;
}

.scrolled{
  #main-content{
    margin-top: -50px;
  }
}
